.modal {
  width: 720px;
  display: table;
  position: fixed;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  cursor: pointer;
  z-index: 110;
  margin: auto;
  background: #1C1C21;
  border-radius: 40px;
}

.modal-close {
  position: absolute;
  top: 60px;
  right: 40px;
}